<template>
    <el-dropdown>
        <span class="el-dropdown-link">
            <i class="el-icon-more operation"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="move"></el-dropdown-item>
            <el-dropdown-item @click.native="copy" v-if="scope.row.type !== 'folder'"></el-dropdown-item>
            <el-dropdown-item @click.native="rename"></el-dropdown-item>
            <el-dropdown-item @click.native="download" v-if="scope.row.type !== 'folder'"></el-dropdown-item>
            <el-dropdown-item @click.native="deleteResource"></el-dropdown-item>
        </el-dropdown-menu>
    </el-dropdown>
</template>

<script>
import { renameFile, deleteFile } from '@/api/resource'
export default {
    props: ['scope'],
    methods: {
        move: function(){
            this.$store.commit('openFileTreeDialog', {
                title: '[' + this.scope.row.name + '] ',
                id: this.scope.row.id,
                type: 'move'
            })
        },
        copy: function(){
            this.$store.commit('openFileTreeDialog', {
                title: '[' + this.scope.row.name + '] ',
                id: this.scope.row.id,
                type: 'copy'
            })
        },
        download: function(){
            this.$message({
                message: '',
                type: 'warning',
                duration: 3 * 1000
            })
        },
        rename: function(){
            // TODO 
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputValue: this.scope.row.name
            }).then(({value}) => {
                renameFile(this.scope.row.id, value).then(() => {
                    this.$emit('flush')
                })
            }).catch(() => {})
        },
        deleteResource () {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                deleteFile(this.scope.row.id).then(() => {
                    this.$emit('flush')
                })
            }).catch(() => {});
        }
    }
}
</script>

<style lang="scss" scoped>
.el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
}
.el-icon-arrow-down {
    font-size: 12px;
}
</style>
